*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style: none;
}

input {
  border: none;
  outline: none;
}

button {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit;
}

:root {
  --color-text-primary: #000;
  --color-text-secondary: #7e7e7e;
  --background-color-primary: #d7d7d7;
  --background-color-secondary: #7a7a7a;
  --background-color-tertiary: #f1f1f1;
  --background-color-fourth: #d9d9d9;
  --background-color-chosen-scientist: #cacaca;
  --color-button: #fff;
  --color-true: #039900;
  --color-false: #900;
  --color-backdrop: rgba(0, 0, 0, .443);
  --modal-dinosaur: rgba(120, 120, 120, .655);
  --shadow: rgba(0, 0, 0, .25);
  --soccer-field: #5abb58;
  --main-background: #f0f0f0;
  --color-dot: #bbb;
  --background-dot: #717171;
}

[data-theme="dark"] {
  --color-text-primary: #fff;
  --color-text-secondary: #444;
  --background-color-primary: #413e3e;
  --background-color-secondary: #d3d3d3;
  --background-color-tertiary: #393939;
  --background-color-fourth: #2c2c2c;
  --background-color-chosen-scientist: #323232;
  --color-button: #2a2a2a;
  --color-true: #0f0;
  --color-false: red;
  --color-backdrop: rgba(0, 0, 0, .82);
  --modal-dinosaur: rgba(80, 80, 80, .718);
  --shadow: rgba(255, 255, 255, .15);
  --soccer-field: #4a9147;
  --main-background: #2a2a2a;
  --color-dot: #888;
  --background-dot: #555;
}

html {
  scroll-behavior: smooth;
}

img {
  width: 100%;
  max-width: 100%;
  display: block;
}

body {
  color: var(--color-text-primary);
  padding-bottom: 30px;
  font-family: Montserrat Alternates, sans-serif;
  font-size: 12px;
  font-weight: 400;
}

html[data-theme="dark"] body {
  background-color: var(--color-button);
}

input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

label, .hidden {
  display: none;
}

.no-scroll {
  overflow: hidden;
}

main {
  background-color: var(--main-background);
  flex-direction: column;
  gap: 36px;
  margin: 0 auto;
  display: flex;
}

main.img {
  background-image: url("background.430f95c7.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

main.fiximg {
  max-width: 1440px;
  background-size: cover;
}

.title {
  text-align: center;
  margin-bottom: 36px;
  font-family: Montserrat Alternates, sans-serif;
  font-size: 16px;
  line-height: 1.25;
}

.container {
  max-width: 1090px;
  margin: 0 auto;
}

.hidden {
  display: none;
}

input {
  color: var(--color-text-secondary);
  background-color: var(--background-color-primary);
}

.game__container {
  padding: 36px 0;
  position: relative;
}

.game__container:after {
  content: "";
  background-color: var(--color-text-primary);
  height: 1px;
  width: 536px;
  position: absolute;
  bottom: -36px;
  left: 50%;
  transform: translate(-50%, -50%);
}

section:last-child .game__container:after {
  display: none;
}

.calculator-year__container, .guess-number__container {
  max-width: 576px;
}

.google-dinosaur, .soccer {
  max-width: 720px;
  margin: 0 auto;
}

.scientist__button, .footer__button, .calculator__operator {
  color: var(--color-button);
  background-color: var(--color-text-primary);
}

.calculator {
  flex-direction: column;
  align-items: center;
  gap: 35px;
  margin: 0 auto;
  display: flex;
}

.calculator__input {
  text-align: center;
  max-width: 135px;
  border-radius: 20px;
  padding: 10px 20px;
  line-height: 1.25;
  box-shadow: 3px 3px rgba(0, 0, 0, .25);
}

.calculator__operators {
  max-width: 62px;
  flex-wrap: wrap;
  justify-content: space-between;
  row-gap: 10px;
  display: flex;
}

.calculator__operator {
  text-align: center;
  max-width: 25px;
  max-height: 25px;
  border-radius: 50%;
  flex-basis: calc(50% - 5px);
  justify-content: center;
  padding: 5px 10px;
  display: flex;
  box-shadow: 3px 3px rgba(0, 0, 0, .25);
}

.calculator__main {
  align-items: center;
  gap: 38px;
  display: flex;
}

.header {
  background-color: var(--color-button);
}

.header__container {
  justify-content: space-between;
  align-items: center;
  gap: 80px;
  padding: 32px 0;
  display: flex;
}

.header__logo {
  width: 93px;
  height: 38px;
}

.header__nav {
  width: 100%;
  align-items: center;
  display: flex;
}

html[data-theme="dark"] .header__logo {
  filter: brightness(0) saturate() invert() sepia() saturate(0%) hue-rotate(129deg) brightness(106%) contrast(101%);
}

.menu__list {
  gap: 50px;
  display: flex;
}

.nav__list {
  width: 100%;
  justify-content: space-between;
  display: flex;
}

.nav-list__wrapper {
  gap: 50px;
  display: flex;
}

.links__item {
  position: relative;
}

.links__link {
  font-size: 12px;
}

.links__link--interactive {
  align-items: center;
  gap: 10px;
  display: flex;
}

.links__link--interactive:after {
  content: "";
  width: 11px;
  height: 7px;
  background-image: url("arrow.0540d441.svg");
  background-repeat: no-repeat;
  display: flex;
}

html[data-theme="dark"] .links__link--interactive:after {
  filter: brightness(0) saturate() invert() sepia() saturate(0%) hue-rotate(129deg) brightness(106%) contrast(101%);
}

.submenu {
  min-width: 160px;
  visibility: hidden;
  opacity: 0;
  background-color: var(--color-button);
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  padding-top: 20px;
  transition: opacity .25s ease-in-out, visibility .25s ease-in-out;
  position: absolute;
}

.submenu__wrapper {
  border: 1px solid var(--color-text-primary);
  background-clip: padding-box;
  border-top: none;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.submenu__link {
  color: var(--color-text-primary);
}

.submenu__item {
  text-align: center;
  padding: 10px 50px;
}

.submenu__item:hover {
  background-color: rgba(0, 0, 0, 0);
}

.submenu__item:last-child:hover {
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.links__item--interactive:hover .submenu {
  opacity: 1;
  visibility: visible;
}

.menu__login {
  align-items: center;
  display: flex;
}

.switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: var(--background-color-secondary);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 20px;
  width: 20px;
  background-color: var(--color-button);
  border: .5px solid var(--color-text-primary);
  background-image: url("sun.4d402e86.svg");
  background-position: center;
  background-repeat: no-repeat;
  transition: all .4s;
  position: absolute;
}

input:checked + .slider:before {
  background-color: #000;
  background-image: url("moon.e6f90b43.svg");
  transform: translateX(26px);
}

.slider {
  border-radius: 34px;
}

.slider:before {
  border-radius: 50%;
}

.soccer__yard {
  width: 720px;
  height: 220px;
  background: var(--soccer-field);
  border: 2px solid var(--color-text-primary);
  border-radius: 37px;
  position: relative;
  overflow: hidden;
}

.ball {
  width: 50px;
  height: 50px;
  transform-origin: 0 0;
  background-image: url("football.baa50234.svg");
  transition-property: top, left;
  transition-duration: 2s;
  transition-timing-function: ease-out;
  position: absolute;
  top: 100px;
  left: 92px;
  transform: translate(-50%, -50%);
}

.rotate {
  animation: rotateBall 1.5s ease-out infinite;
}

@keyframes rotateBall {
  0% {
    rotate: none;
  }

  100% {
    rotate: 360deg;
  }
}

.biggest-number {
  text-align: center;
}

.maximum__inputs {
  justify-content: center;
  gap: 38px;
  margin-bottom: 36px;
  display: flex;
}

.maximum__input {
  max-width: 135px;
  text-align: center;
  background-color: var(--background-color-primary);
  color: var(--color-text-secondary);
  box-shadow: 3px 3px 0 0 var(--shadow);
  border-radius: 20px;
  padding: 10px 20px;
}

.time-calculator {
  justify-content: center;
  display: flex;
}

.time-calculator__formcontainer {
  justify-content: space-between;
  align-items: center;
  gap: 33px;
  display: flex;
}

.time-calculator__formcontainer:after {
  content: "- - - - - - - - - - - - -";
  align-items: center;
  display: flex;
}

.time-calculator__form {
  box-shadow: 3px 3px 0 0 var(--shadow);
  border-radius: 20px;
  display: flex;
}

.time-calculator__input {
  text-align: center;
  max-width: 150px;
  background-color: var(--background-color-primary);
  color: var(--color-text-secondary);
  border-radius: 20px 0 0 20px;
  padding: 10px 45px 10px 10px;
  font-size: 12px;
}

html[data-theme="dark"] .time-calculator__button {
  background-color: var(--color-text-primary-dark);
}

.time-calculator__button {
  width: 35px;
  height: 35px;
  background-image: url("search_icon.3f7c4591.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 15px;
  background-color: var(--color-text-primary);
  border-radius: 0 20px 20px 0;
  padding: 10px;
}

.time-calculator_result {
  order: 2;
}

.backdrop {
  background-color: var(--color-backdrop);
  opacity: 1;
  z-index: 998;
  transition: opacity .25s ease-in-out;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  transition: opacity .25s ease-in-out;
}

.no-scroll {
  overflow: hidden;
}

.modal {
  z-index: 999;
  background: var(--color-button);
  background-image: url("sword.2f6f2785.svg"), url("puzzle.736d2632.svg"), url("tic-tac-toe.3ae5d409.svg"), url("game-development.32b4b345.svg");
  background-position: 10% 56%, 20% 84%, 80% 84%, 90% 56%;
  background-repeat: no-repeat;
  background-size: 30px;
  border-radius: 20px;
  padding: 45px 92px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%)scale(1);
}

.greeting__info {
  flex-direction: column;
  gap: 5px;
  margin-bottom: 40px;
  display: flex;
}

.greeting__userinfo {
  flex-direction: column;
  align-items: center;
  gap: 18px;
  display: flex;
}

.info__title, .info__paragraph {
  text-align: center;
  font-family: Inter, sans-serif;
  font-weight: 400;
}

.user-info__label {
  text-align: center;
  font-family: Inter, sans-serif;
}

.user-info__input {
  max-width: 170px;
  color: var(--color-text-secondary);
  background-color: var(--background-color-primary);
  border-radius: 20px;
  align-items: center;
  padding: 10px 90px 10px 10px;
}

.user-info__button {
  color: var(--color-button);
  background-color: var(--color-text-primary);
  border-radius: 20px;
  padding: 10px 20px;
}

.button__close {
  position: absolute;
  top: 30px;
  right: 30px;
}

.close__svg {
  width: 17px;
  height: 17px;
}

.footer {
  background-color: var(--color-button);
  padding: 60px 0;
}

.footer__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.footer__logo {
  width: 165px;
  height: 70px;
}

.footer__contacs {
  flex-direction: column;
  align-items: flex-start;
  gap: 0;
  display: flex;
}

.footer__contact {
  line-height: 170%;
}

.footer__form {
  justify-content: space-between;
  gap: 36px;
  padding-bottom: 25px;
  display: flex;
  position: relative;
}

.footer__input {
  max-width: 204px;
  border-radius: 20px;
  padding: 10px 80px 10px 10px;
}

.footer__button {
  border-radius: 20px;
  padding: 10px;
}

.footer__agreement {
  font-size: 6px;
  position: absolute;
  bottom: 0;
  left: 0;
}

html[data-theme="dark"] .footer__logo {
  filter: brightness(0) saturate() invert() sepia() saturate(0%) hue-rotate(129deg) brightness(106%) contrast(101%);
}

.footer-menu__container {
  width: 463px;
  height: 245px;
  background-image: url("sword.2f6f2785.svg"), url("puzzle.736d2632.svg"), url("tic-tac-toe.3ae5d409.svg"), url("game-development.32b4b345.svg");
  background-position: 10% 36%, 20% 64%, 80% 64%, 90% 36%;
  background-repeat: no-repeat;
  background-size: 30px;
  background-color: var(--color-button);
  border-radius: 20px;
  justify-content: center;
  display: flex;
  position: relative;
}

.footer-menu__title {
  text-align: center;
  color: var(--color-text-primary);
  padding-top: 83px;
  font-family: Inter, sans-serif;
  font-size: 20px;
  font-weight: 400;
}

.footer-menu__close {
  position: absolute;
  top: 30px;
  right: 30px;
}

.backdrop {
  z-index: 5000;
  background-color: rgba(0, 0, 0, .443);
  justify-content: center;
  align-items: center;
  animation: backdrop-animation .2s ease-in-out both;
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.scientist {
  flex-direction: column;
  align-items: center;
  gap: 36px;
  display: flex;
}

.scientist__buttons-container {
  flex-direction: column;
  align-items: center;
  gap: 20px;
  display: flex;
}

.scientist__buttons {
  justify-content: space-between;
  display: flex;
}

.scientist__buttons-list {
  flex-direction: column;
  flex-basis: calc(50% - 17.5px);
  gap: 20px;
  display: flex;
}

.scientist--buttons-list-left {
  align-items: flex-end;
}

.scientist__button {
  text-align: left;
  border-radius: 20px;
  padding: 10px 20px;
  box-shadow: 3px 3px rgba(0, 0, 0, .25);
}

.scientist__list {
  max-width: 617px;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 36px 59px;
  display: flex;
}

.scientist__item {
  min-height: 110px;
  background: var(--background-color-fourth);
  min-width: 110px;
  padding: 36.4px 0;
  border-radius: 20px;
  flex-basis: calc(25% - 44.25px);
  padding-left: 22px;
  padding-right: 22px;
}

.scientist-card__meta {
  text-align: center;
  flex-direction: column;
  align-items: center;
  font-size: 8px;
  line-height: 170%;
  display: flex;
}

.scientist-one-card {
  min-width: 100px;
  min-height: 100px;
  background: var(--background-color-fourth);
  border-radius: 20px;
  flex-basis: auto;
}

.game__container--dino {
  width: 100%;
  height: 200px;
  height: 300px;
  background-color: rgba(0, 0, 0, 0);
  background-image: url("game-background.070f4332.png");
  background-position-x: 70%;
  background-size: cover;
  position: relative;
  overflow: hidden;
}

.dino {
  width: 60px;
  height: 60px;
  background-image: url("dino.aec42bea.webp");
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  bottom: 10px;
  left: 50px;
}

.cactus {
  width: 30px;
  height: 60px;
  background-image: url("cactus.62eaa82c.webp");
  background-repeat: no-repeat;
  background-size: contain;
  animation: moveObstacle 2s linear infinite;
  position: absolute;
  bottom: 0;
  right: 0;
}

.dinosaur__modal {
  background-color: var(--modal-dinosaur);
  opacity: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 15px;
  transition: opacity .25s ease-in-out;
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.dinosaur__lose {
  font-size: 18px;
}

.dinosaur__restart {
  border: 1px solid var(--background-color-secondary);
  border-radius: 10px;
  padding: 15px 20px;
  transition: transform .25s ease-in-out;
}

.dinosaur__restart:hover {
  transform: scale(1.2);
}

.dinosaur__modal.show {
  opacity: 1;
  transition: opacity .25s ease-in-out;
}

@keyframes moveObstacle {
  from {
    right: -20px;
  }

  to {
    right: 100%;
  }
}

.jump {
  animation: jump .5s ease-out;
}

@keyframes jump {
  0% {
    translate: 0;
  }

  50% {
    translate: 0 -100px;
  }

  100% {
    translate: 0;
  }
}

.rock-paper-scissors__container {
  flex-direction: column;
  align-items: center;
  gap: 36px;
  display: flex;
}

.rock-paper-scissors__title {
  margin-bottom: 0;
}

.rock-paper-scissors__button {
  background-color: var(--color-text-primary);
  background-image: url("scissors.90f2adbb.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 40px;
  border-radius: 20px;
  padding: 30px;
}

.rock-paper-scissors__button--paper {
  background-image: url("paper.c947b7e8.svg");
}

.rock-paper-scissors__button--rock {
  background-image: url("stone.ae654bb8.svg");
}

html[data-theme="dark"] .rock-paper-scissors__button {
  background-color: var(--color-text-primary-dark);
}

.rock-paper-scissors {
  width: 100%;
  display: flex;
}

.rock-paper-scissors__buttons {
  width: 50%;
  justify-content: flex-end;
  gap: 38px;
  margin: 0 auto;
  display: flex;
}

.score__container {
  width: 25%;
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.result__container {
  flex-direction: column;
  align-items: center;
  gap: 36px;
  display: flex;
}

.result__text {
  text-align: center;
}

.result__button {
  background-color: var(--color-text-primary);
  color: var(--color-button);
  cursor: pointer;
  border-radius: 20px;
  align-content: center;
  padding: 10px 20px;
}

.calculator-year {
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.form__check-year {
  box-shadow: 3px 3px 0px 0px var(--shadow);
  border-radius: 20px;
  display: flex;
}

.input__check-year {
  width: 180px;
